import { cx } from '@emotion/css';
import { imageStorage } from 'common';
import React from 'react';
import { useInView } from 'react-intersection-observer';
import { imagePixelatedStyle } from './image.style';

type TImageProps = {
  src: string;
  alt: string;
  className: string;
};

export function Img({ src, alt, className }: TImageProps): JSX.Element {
  const [currentSrc, setCurrentSrc] = React.useState(() => imageStorage.getImageBase64Data(src));
  const [isPixelated, setIsPixelated] = React.useState(true);
  const { ref, inView } = useInView({
    threshold: 0.3,
    triggerOnce: true
  });

  React.useEffect(() => {
    if (inView) {
      const img = new Image();

      img.onload = () => {
        setIsPixelated(false);
        setCurrentSrc(img.currentSrc);
      };

      img.src = src;
    }
  }, [src, inView]);

  return (
    <img
      ref={ref}
      src={currentSrc}
      alt={alt}
      className={isPixelated ? cx(className, imagePixelatedStyle) : className}
    />
  );
}
