import { imageStorage } from 'common';
import React from 'react';
import { choosePageImageStyle } from './choose-page.style';

type TChoosePageImageProps = {
  imageUrl: string;
};

export function ChoosePageImage({ imageUrl }: TChoosePageImageProps): JSX.Element {
  const [currenSrc, setCurrentSrc] = React.useState(imageStorage.getImageBase64Data(imageUrl));
  const [isPixelated, setIsPixelated] = React.useState(true);

  React.useEffect(() => {
    setCurrentSrc(imageStorage.getImageBase64Data(imageUrl));
    setIsPixelated(true);

    const img = new Image();
    img.onload = () => {
      setCurrentSrc(img.currentSrc);
      setIsPixelated(false);
    };

    img.src = imageUrl;
  }, [imageUrl]);

  return (
    <div className={choosePageImageStyle(currenSrc, isPixelated)} />
  );
}
