export class ImageStorage {
  private storage = new Map<string, string>();

  setImageBase64Data(imageUrl: string, base64data: string): void {
    this.storage.set(imageUrl, base64data);
  }

  getImageBase64Data(imageUrl: string): string {
    return this.storage.get(imageUrl) || imageUrl;
  }
}

export const imageStorage = new ImageStorage();
